/* Home Page Styling */

/* Fullscreen background */
#background {
	position: relative;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: -1;
}

/* Horizontal divider */
#divider {
	position: relative;
	height: 100px;
	width: 100%;
	background-color: black;
	transform: skewY(-10deg);
}

#divider::before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: white;
	transform: skewY(10deg);
}

/* Designer and Developer text */
#designer {
	position: absolute;
	top: 50%;
	left: 20%;
	transform: translateY(-50%);
	font-family: "Roboto", sans-serif;
	font-size: 3rem;
	color: white;
	cursor: pointer;
}

#developer {
	position: absolute;
	top: 50%;
	right: 20%;
	transform: translateY(-50%);
	font-family: "Roboto", sans-serif;
	font-size: 3rem;
	color: black;
	cursor: pointer;
}

/* Hover effect */
#designer:hover {
	background-color: white;
	color: black;
	transform: translate(-30%, -50%);
	transition: all 0.3s ease-in-out;
}

#designer:hover #divider {
	width: 130%;
	transition: all 0.3s ease-in-out;
}

#developer:hover {
	background-color: black;
	color: white;
	transform: translate(30%, -50%);
	transition: all 0.3s ease-in-out;
}

#developer:hover #divider {
	width: 130%;
	transition: all 0.3s ease-in-out;
}

.canvas-container {
	position: absolute;
}

.divider {
	position: relative;
}
